.TopBanner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    margin-bottom: 20px;
}
  
.TopBanner img {
    max-width: 100%;
    max-height: 100%;
}
  
@media screen and (max-width: 600px) {
    .TopBanner {
    height: 80px;
    }

    .TopBanner img {
    max-height: 60%;
    margin-top: 10px;
    }
}
  