.Services {
  padding: 20px;
  text-align: center;
}

.ServicesTitle {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
}

.ServicesContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  max-width: 1200px;
}

@media screen and (max-width: 1150px) {
  .ServicesContainer {
    flex-direction: column;
  }
}
