.MainBanner {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 500px;
    background-size: cover;
    background-position: center 70%;
    background-repeat: no-repeat;
    border-radius: 10px;
  }
  
  .leftImage,
  .rightImage {
    width: 33.333%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .leftImage img,
  .rightImage img {
    max-height: 80%;
  }
  
  .title {
    width: 33.333%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'GTA', sans-serif;
  }
  
  .title h2 {
    font-size: 2.5rem;
    color: #fff;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  }
  
  @media screen and (max-width: 768px) {
    .MainBanner {
      height: 450px;
    }
  
    .leftImage,
    .rightImage {
      width: 100%;
      height: 40%;
    }
  
    .title {
      width: 100%;
      height: 40%;
      font-size: 1.5rem;
    }
  
    .title h2 {
      font-size: 1.5rem;
    }
  }
  