.FirstSection {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 40px;
    background-color: #292929;
    color: white;
    text-align: left;
    margin-top: 20px;
    border-radius: 10px;
  }
  
  .FirstSectionTitle {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 20px;
    align-self: center;
  }
  
  .FirstSectionSeparator {
    width: 80%;
    border: 1px solid white;
    margin-bottom: 20px;
  }
  
  .FirstSectionText1 {
    font-size: 24px;
    font-weight: bold;
    line-height: 1.5;
    max-width: 800px;
  }
  
  .FirstSectionText2 {
    font-size: 18px;
    line-height: 1.5;
    max-width: 800px;
    opacity: 0.7;
  }
  
  @media (max-width: 768px) {
    .FirstSectionTitle {
      font-size: 28px;
    }
  
    .FirstSectionText1 {
      font-size: 16px;
    }
  
    .FirstSectionText2 {
      font-size: 14px;
    }
  }
  
  @media (max-width: 480px) {
    .FirstSectionTitle {
      font-size: 24px;
    }
  
    .FirstSectionText1 {
      font-size: 14px;
    }
  
    .FirstSectionText2 {
      font-size: 12px;
    }
  
    .FirstSectionSeparator {
      width: 60%;
    }
  }